@import "../../index.scss";

.navbar{
  background: #56041f;
  padding:0.5rem 1rem;
  display:grid;
  grid-template-columns: auto 1fr;
}

.nav-ul-links{
  list-style-type: none;
  align-self: center;
  display:grid;
  grid-template-columns: repeat(3,1fr);
  text-align:center;
}

.nav-link{
  color:$mainGrey;
  text-transform: capitalize;
  text-decoration: none;
  font-size: 1.2rem;
  transition: all 0.3s linear;
  &:hover{
    color:$mainGreen;
  }
}
